ul {
    background-color: #004BA4;
}

#nav-img{
    max-width: 500px;
    max-height: 120px;
    width: auto;
    height: auto;
}
.card-wrapper{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    padding: 20px 60px;
}
.messageWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.img-custom {
    max-height: 150px;
    max-width: 150px;
    height: auto;
    width: auto;
    background-color: white;
    border-radius: 20px;
}

.character-card{
    background-color: #004BA4;
    border: 1px solid #004BA4;
    border-radius: 30px;
    height: 180px;
    width: 180px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px 30px;
}
footer{
    background-color: #004BA4;
    height: 120px;
}
.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.content {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
